.section {
  --section-bg: transparent;
  background: var(--section-bg);
  position: relative;
  padding: 5rem 0;
}

.section-sm {
  padding: 4rem 0;
}

.section-white {
  --section-bg: var(--tblr-bg-surface);
}

.section-light {
  --section-bg: var(--tblr-bg-surface-secondary);
}

.section-primary {
  --section-bg: var(--tblr-primary);
  color: #ffffff;
}

.section-dark {
  --section-bg: var(--tblr-dark);
  color: #ffffff;
}

.section-header {
  text-align: center;
  max-width: 45rem;
  margin: 0 auto 5rem;
}
.section-sm .section-header {
  margin-bottom: 4rem;
}

.section-title {
